<template>
<div>
    <!-- <div class="bg-green-600" v-if="$vuetify.breakpoint.mobile">
        <br><br> 
    </div> -->
    <v-app-bar class="bg-alpha flex row-wrap overflow-y-hidden overflow-x-auto" app>
        <v-app-bar-nav-icon v-if="!backMode" dark @click="navbarOpen = !navbarOpen" class="d-flex d-md-none"></v-app-bar-nav-icon>
        <v-btn v-else @click="$router.go(-1)" x-large text dark fab class="d-flex d-md-none">
            <v-icon>mdi-arrow-left</v-icon>
        </v-btn>

        <v-toolbar-title class="text-white">พันธุ์ควายไทย</v-toolbar-title>
        <v-btn class="d-none d-md-flex ml-3" text dark v-for="(menu, i) in menus" :key="i" @click="$router.push(menu.path)">
            <img class="h-8 w-auto" :src="menu.image" alt="" />
            <span class="ml-2">
                {{ menu.name }}
            </span>
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn dark class="d-none d-md-flex" outlined @click="logout()">ออกจากระบบ</v-btn>
    </v-app-bar>

    <v-bottom-sheet v-model="navbarOpen" inset>
        <v-sheet height="253px ">
            <div class="flex flex-col">
                <v-btn v-for="(menu, i) in menus" :key="i" text @click="goToPath(menu.path)">
                    <img class="h-8 w-auto" :src="menu.image" alt="" />
                    <span class="ml-2">
                        {{ menu.name }}
                    </span>
                </v-btn>
                <v-btn color="error" @click="logout()">ออกจากระบบ</v-btn>
            </div>
        </v-sheet>
    </v-bottom-sheet>
</div>
</template>

<script>
import UserDropdown from "@/components/Dropdowns/UserDropdown.vue";
import { Auth } from "@/store/auth";
export default {
    data() {
        return {
            navbarOpen: false, 
            menus: [

                {
                    name: "หน้าแรก",
                    image: "./assets/farm/2826134-agriculture/png/005-farm.png",
                    path: "/user/home",
                },
                {
                    name: "ข้อมูลส่วนตัว",
                    image: "./assets/farm/2826134-agriculture/png/006-farmer.png",
                    path: "/user/profile",
                },
                {
                    name: "ใบพันธุ์ประวัติ",
                    image: "./assets/farm/2826134-agriculture/png/076-award.png",
                    path: "/user/search",
                },
                {
                    name: "ควายของฉัน",
                    image: "./assets/buffalo/buffalo.png",
                    path: "/user/buffalo",
                },
                {
                    name: "แผนที่",
                    image: "./assets/farm/2826134-agriculture/png/054-truck.png",
                    path: "/user/map",
                },
                {
                    name: "รายงาน",
                    image: "./assets/farm/2826134-agriculture/png/088-date.png",
                    path: "/user/reportbuffalo",
                },
            ],
        };
    },
    async created() {
        if (this.$route.name != 'หน้าแรก') {
            this.backMode = true
        } else {
            this.backMode = false
        }
        console.log(this.$route.name)
    },
    computed: {
        backMode() {
            if (this.$route.name != 'หน้าแรก') {
                return true
            } else {
                return false
            }
        }
    },
    methods: {
        setNavbarOpen: function () {
            this.navbarOpen = !this.navbarOpen;
        },
        logout: async function () {
            await Auth.logout();
            await location.reload();
        },
        async goToPath(path) {
            await this.$router.push(path);
            this.navbarOpen = false;
        },
    },
    components: {
        UserDropdown,
    },
};
</script>

